import Dialog from '@/components/common/Dialog.vue'
import T from '@/utils/tips.js'
export default {
	data() {
		return {
			title: '确认删除所有历史记录吗?',
			isShow: false,
			search: "",
			records: [],
			loading: false,
			index: '',
			platform: 0,
		};
	},
	components:{
		Dialog
	},
	onLoad() {
		// 设备样式兼容
		this.platform = uni.getStorageSync('platform');
	},
	onShow() {
		this.getRecord();
	},
	methods: {
		doConfirm() {
			let records = this.records;
			if (this.index == 1) {
				index = records.indexOf(item);
				records.splice(index, 1);
				uni.setStorageSync("records", JSON.stringify(records));
			} else {
				records.splice(0, records.length);
				uni.setStorageSync("records", JSON.stringify(records));
			}
			this.isShow = false
		},
		doCancel() {
			this.isShow = false
		},
		toSearch(e, data) {
			if (data) {
				this.search = data;
			}
			if (!this.search) {
				return T.tips("请输入搜索内容");
			}
			this.setRecord(this.search);
			uni.navigateTo({
				url:'/pages/order/goodsList/goodsList?search='+this.search
			})
		},
		getRecord() {
			let records = uni.getStorageSync("records");
			if (records) {
				try {
					records = JSON.parse(records);
				} catch (e) {
					return console.log(e);
				}
				if (records instanceof Array) {
					this.records = records;
				}
			}
		},
		setRecord(item) {
			let records = this.records;
			let index = records.indexOf(item);
			if (index > -1) {
				records.splice(index, 1);
			}
			records.unshift(item);
			uni.setStorageSync("records", JSON.stringify(records));
		},
		delRecord(e, item) {
			let records = this.records;
			let index;
			if (item !== undefined) {
				this.index = 1
			} else {
				this.index = 2
			}
			this.isShow = true
		}
	}
}
